
export function mainRender(state) {  //////// —Ñ—É–Ω–∫–∏—è –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –ø—Ä–æ–≤–µ—Ä—è–µ—Ç –µ—Å–ª–∏ –µ—Å—Ç—å –≤ –ª–æ–∫–∞–ª—å–Ω–æ–º —Ö—Ä–∞–Ω–∏–ª–∏—â–µ —Ç–∞–∫—Å–∏ , –∑–∞–ø–æ–ª–Ω—è–µ—Ç state

    if (localStorage.length >= 1) { //////// –ø—Ä–æ–≤–µ—Ä—è–µ—Ç –µ—Å–ª–∏ –µ—Å—Ç—å –≤ –ª–æ–∫–∞–ª—å–Ω–æ–º —Ö—Ä–∞–Ω–∏–ª–∏—â–µ –±–æ–ª—å—à–µ 0 –∑–∞–¥–∞—á
        for (let i = 0; i < localStorage.length; i++) { //////// –µ—Å–ª–∏ –µ—Å—Ç—å –∑–∞–ø—É—Å–∫–∞–µ—Ç —Ü–∏–∫–ª
            let key = localStorage.key(i);
            state.push(JSON.parse(localStorage.getItem(key))) //////// –ø–µ—Ä–µ–≤–æ–¥–∏ –∏–∑ json —Ñ–æ—Ä–º–∞—Ç–∞ –Ω–∞—à–∏ —Ç–∞–∫—Å–∏ –∏ –∑–∞–ø–æ–ª–Ω—è–µ–º state
        }
    }
}

